var that;
var arr = [];
export default {
	data() {
		return {
			lodstatus:'',//loading,more,noMore
			CustomBar: this.CustomBar,
			page: 1,
			list: [],
			keyword: '',
			nolist: false,
			local: [],
			localshow: true,
			modal: false,
			page_count:'',
			row_count:''
		};
	},
	methods: {
		getlist(e) { 
			that.lodstatus='loading';
			that.nolist=false
			this.$request
				.request(
					{
						url: that.api.default.search, 
						data: {
							page: that.page,
							keyword: that.keyword
						},
						Toast:'正在搜索' 
					} 
				)
				.then(res => {  
					var list = res.list; 
					if(that.page==1){
						that.list=list;
						that.page++
						list.length==0?(that.lodstatus='',that.nolist=true):(list.length>10?(that.lodstatus='more'):(that.lodstatus=''))
					}else{
						if(list.length>0){
							that.list=that.list.concat(list);
							that.page++;
							that.lodstatus='more';
						}else{
							that.lodstatus='noMore';
						} 
					}
					that.page_count=res.page_count;
					that.row_count=res.row_count
					
				});
		},
		del() {
			that.local = [];
			uni.setStorageSync('local', that.local); 
		}, 
		//获取显示历史记录
		fo() {
			var local = uni.getStorageSync('local');
			// 历史记录倒序
			if (local) {
				local.reverse();
				that.local = local;
				that.localshow = true;
			} else {
				uni.setStorageSync('local', []);
			}
		},
		searchList(e) {
			//console.log(this.local[e])
			that.keyword = that.local[e];
			that.page = 1;
			that.list = [];
			that.getlist();
			that.nolist = false;
			// 隐藏历史记录
			that.localshow = false;
		},
		getsearch(e) {
			that.keyword = e.detail.value;
			that.nolist=false;
			if (that.keyword.length == 0){
				that.localshow = true;
				that.fo();
			}
		},
		search(e) {
			that.page = 1;
			that.list = [];
			that.nolist = false;
			that.getlist();  
			that.localshow = false;
			var val =that.keyword; 
			val = val?val.trim():val;
			if (val!= '') {
				if (uni.getStorageSync('local') != []) {
					arr = uni.getStorageSync('local');
					// 不重复添加值
					if (arr.indexOf(val) == -1) {
						arr.push(val);
					}
					//最多显示15条历史记录
					if (arr.length > 15) {
						arr.shift();
					}
				} else {
					// 不重复添加值
					if (arr.indexOf(val) == -1) {
						arr.push(val);
					}
				}
			}
			uni.setStorageSync('local', arr);
			that.local=arr;
			
		}
	},

	onLoad() {
		that = this;
		// 显示历史记录
		that.fo();
	},
	onReachBottom() {
		that.getlist();
	}
};
